<template>
  <el-row type="flex" justify="center">
    <el-col :span="6" class="grid-content">
      <div>
        <ul class="list-ul">
          <todoitem v-for="(todo,index) in todos" :key="index"
                    :todo="todo"
                    :index="index"
                    :deletetodo="deletetodo"/>
        </ul>
      </div>
    </el-col>
  </el-row>    

</template>

<script>
  import todoitem from './todoitem'
  export default {
    props:{
      todos:Array,
      deletetodo:Function
    },
    components:{
      todoitem
    }
  }
</script>

<style>
  .list-ul li {
    list-style:none
  }
  .list-ul li{
    padding-bottom: 10px  
  }
</style>


