<template>
  <el-row type="flex" justify="center">
    <el-col :span="6" class="grid-content">
      <div>
        <el-checkbox v-model="AllComplete"></el-checkbox>
        <span>已完成{{completeSize}}/全部{{todos.length}}</span>
        <el-button v-on:click="deleteCompleteItem">清除已完成的任务</el-button>
      </div>
    </el-col>
  </el-row>      
</template>

<script>
  export default {
    props:{
      todos:Array
    },
    methods:{
      deleteCompleteItem(){
        this.$emit('deleteCompleteTodos');
      }
    },
    computed:{
      completeSize(){
        return this.todos.reduce((preTotal,todo) => preTotal + (todo.complete?1:0),0)
      },
      AllComplete:{
        get(){
          return this.completeSize === this.todos.length
        },
        set(value){
          this.$emit('selectAllTodo',value);
        }
      }
    },

  }
</script>

<style>

</style>


