<template>
  <li>
    <el-checkbox v-model="todo.complete">{{todo.title}}</el-checkbox>
    <el-button v-on:click="deleteitem">删除</el-button>
  </li>
</template>

<script>
  export default {
    props:{
      todo:Object,
      index:Number,
      deletetodo:Function
    },
    methods:{
      deleteitem(){
        this.deletetodo(this.index)
      }
    }
  }
</script>

<style>

</style>


