<template>
  <el-row type="flex" justify="center">
    <el-col :span="6" class="grid-content">
      <div>
        <el-input placeholder="请输入任务内容" v-model="text">
          <el-button slot="append" v-on:click="additem">提交</el-button>
        </el-input>
      </div>
    </el-col>
  </el-row>    
</template>

<script>
  export default {
    data(){
      return {
        text:""
      }
    },
    methods:{
      additem(){
        const title = this.text.trim();
        if(!title){
          alert("请输入任务内容");
        }
        const todo = {
          title,
          complete:false
        }
        this.$emit('addtodo',todo)
        this.text = ""
      }
    }
  }
</script>

<style>

</style>


